export default {
	data() {
		return {
			havePay: [],
			isActive: true
		}
	},
	onLoad: function(e) {
		uni.request({
			url: 'http://39.105.50.203/search/havePay.json',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res)
				this.havePay = res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	}

}
